body{
    display: flex;
    margin: 0;
    align-items: center;
    justify-content: center;
    background-color: hsl(216, 12%, 8%);
    height: 100vh;
    overflow: hidden;
    font-family: 'Overpass', sans-serif;
}

.main{
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(160deg, hsl(213, 19%, 18%),hsl(216, 12%, 8%));
    box-shadow: 0 17px 19px 0 rgba(0, 0, 0, 0.2), 0 16px 120px 0 rgba(0, 0, 0, 0.19);
    border-radius: 25px;
    padding: 2.5rem;
    width: 22.5rem;
    height: 22.5rem;
}

.main h1{
    color: white;
    margin-bottom: 0;
}

.main p {
    margin-top: 0;
    color: hsl(217, 12%, 63%);
    line-height: 1.5rem;
}

.beforeSubmit{
    display: flex;
    flex-direction: column;
    gap: 1rem;
  
}

.ratingComponent{
    display: flex;
    gap: 1.5rem;
    align-items: center;
    justify-content: center;
    padding-bottom: 1rem;
}

.ratingCircle{
    width: 3.2rem;
    height: 3.2rem;
    border-radius: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: hsl(213, 19%, 18%);
    color: hsl(217, 12%, 63%);
    user-select: none;
    transition: background-color 0.2s, color 0.2s;
}

.ratingCircle:hover{
    background-color: hsl(25, 97%, 53%);
    color: white;
}

.submitBtn{
    display: flex;
    justify-content: center;
    margin-top: 1rem;
}

.submitBtn button{
    font-size: 14px;
    border-radius: 25px;
    padding: .8rem 9.5rem;
    letter-spacing: .1rem;
    outline: none;
    border: none;
    background-color:  hsl(25, 97%, 53%);
    color: white;
    transition: background-color 0.2s, color 0.2s;
}

.submitBtn button:hover{
    color:hsl(25, 97%, 53%) !important;
    background-color: white !important;
}

/* After submission      */

.afterSubmit{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1rem;
}

.thankyouComponent{
    padding-bottom: 1rem;
}

.hidden{
    display: none !important;
}

.finalRatingComponent{
    display: flex;
    align-items: center;
    justify-content: center;
}

.finalRatingComponent p{
    background-color: hsl(213, 19%, 18%);
    color: hsl(25, 97%, 53%);
    padding: .25rem 1rem;
    border-radius: 25px;
    text-align: center;
    margin-bottom: 0;
    width: fit-content;
}

#innerAfter-text{
    text-align: center;
}

.warningContainer{
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    width: 50%;
    bottom: 9.5rem;
    right: 26.6rem;
}

.warningContainer p{
    color: rgb(249, 88, 88);
}


@media screen and (max-width:400px) {

    .main{
        width: 15.5rem;
        padding: 1rem 2.5rem;
    }

    .main h1{
        font-size: 24px;
    }

    .main p{
        font-size: 14px;
    }

    #logo{
        width: 2.5rem;
    }

    .ratingComponent {
        gap: 1rem;
    }

    .ratingCircle{
        width: 4rem;
        height: 2.5rem;
    }

    .submitBtn button{
        padding: .8rem 7rem;
    }

    .beforeSubmit{
        width: 17.5rem;
    }    

    #innerAfter-text{
        width: 18rem;
        margin: 0;
    }
}